<script>
// import { Line } from 'vue-chartjs';
import { Line } from 'vue-chartjs';
import 'chartjs-plugin-zoom';
// eslint-disable-next-line no-unused-vars
import _ from 'lodash';
// import test from '../assets/test.json';
import moment from 'moment';
// import middata from '../assets/midatalinechart.json';

export default {
  // extends: Line,
  extends: Line,
  props: {
    switchClass: Array,
  },
  data() {
    return {
      total: [],
      className: '',
    };
  },
  mounted() {
    this.className = this.midatalinechart[0].classroom;
    this.midatalinechart.forEach((element) => {
      this.total.push({
        y: element.use_total_time.toFixed(2),
        x: moment(new Date(element.date)).format('YYYY-MM-DD'),
      });
    });
    this.renderChart(
      {
        datasets: [
          {
            label: '',
            data: this.total,
            backgroundColor: 'transparent',
            borderColor: 'rgba(1, 116, 188, 0.50)',
            pointBackgroundColor: 'rgba(171, 71, 188, 1)',
          },
        ],
      },
      {
        responsive: true,
        maintainAspectRatio: false,
        title: {
          display: true,
          text: '使用總時間折線圖',
          fontFamily: '微軟正黑體',
          fontSize: 30,
        },
        legend: {
          // 不要顯示資料集的按鈕
          display: false,
        },
        tooltips: {
          bodyFontSize: 16,
          titleFontSize: 18,
          callbacks: {
            label(tooltipItem, data) {
              // console.log(tooltipItem);
              let label = data.datasets[tooltipItem.datasetIndex].label || '';
              if (label) {
                label += ': ';
              }
              label += Math.round(tooltipItem.yLabel * 100) / 100;
              // console.log(label);
              return `${label}小時`;
            },
          },
        },
        scales: {
          xAxes: [
            {
              type: 'time',
              scaleLabel: {
                display: true,
                labelString: '日期',
              },
              time: {
                unit: 'day',
                displayFormats: {
                  // millisecond: 'YYYY-M-DD',
                  // second: 'YYYY-M-DD',
                  // minute: 'YYYY-M-DD',
                  // hour: 'YYYY-M-DD',
                  day: 'YYYY-M-DD',
                  // week: 'YYYY-M-DD',
                  // month: 'YYYY-M-DD',
                  // quarter: 'YYYY-M-DD',
                  // year: 'YYYY-M-DD',
                },
              },
            },
          ],
          yAxes: [
            {
              scaleLabel: {
                display: true,
                labelString: '使用總時數（小時）',
              },
            },
          ],
        },
        elements: {
          line: {
            fill: false,
            tension: 0,
          },
        },
        plugins: {
          zoom: {
            pan: {
              enabled: true,
              mode: 'x',

              rangeMin: {
                // Format of min pan range depends on scale type
                // x: '2020-04-01',
              },
              rangeMax: {
                // Format of max pan range depends on scale type
                // x: '2020-04-13',
              },
              speed: 20,
              threshold: 10,
            },
            zoom: {
              enabled: true,
              mode: 'x',

              rangeMin: {
                // Format of min pan range depends on scale type
                // x: moment.min(this.midatalinechart.map((e) => moment(new Date(e.date)))),
              },
              rangeMax: {
                // Format of max pan range depends on scale type
                // x: moment.max(this.midatalinechart.map((e) => moment(new Date(e.date)))),
              },
              speed: 0.1,
              sensitivity: 13,
            },
          },
        },
      },
    );
  },
  watch: {
    resetEnv() {
      // console.log('watch');
      // this.$store.dispatch('reset');
      this.$data._chart.resetZoom();
    },
  },
  computed: {
    midatalinechart: {
      get() {
        return this.$store.state.midatalinechart;
      },
    },
    resetEnv: {
      get() {
        return this.$store.state.resetEnv;
      },
    },
  },
};
</script>
