<template>
  <div class="d-flex row overflow-auto">
    <div class="col-xl-6 col-12 flex-column">
      <b-card class="flex-fill m-5 bg-light">
        <b-card-title>電腦使用錯誤次數表</b-card-title>
        <b-table striped hover :fields="fields" :items="midataerrortable" :per-page="perPage2"
          :current-page="currentPage2"></b-table>
        <div class="d-flex flex-fill justify-content-center">
          <b-pagination
            v-model="currentPage2"
            :total-rows="rows2"
            :per-page="perPage2"
            aria-controls="my-table"
          ></b-pagination>
        </div>
      </b-card>
    </div>
    <div class="col-xl-6 col-12">
      <b-card class="flex-fill m-5 bg-light">
        <b-card-title>電腦使用狀況總表</b-card-title>
        <div class="d-flex flex-fill justify-content-center">
          <div>
            <b-form-datepicker
              v-model="date1"
              :min="minDate"
              :max="maxDate"
              class="mb-2"
            ></b-form-datepicker>
          </div>
          <div>
            <b-form-datepicker
              v-model="date2"
              :min="minDate"
              :max="maxDate"
              class="mb-2"
            ></b-form-datepicker>
          </div>
        </div>
        <b-table
         striped hover
          :items="totalData_table2"
          :fields="fields_total"
          :per-page="perPage"
          :current-page="currentPage"
        ></b-table>
        <div class="d-flex flex-fill justify-content-center">
          <b-pagination
            v-model="currentPage"
            :total-rows="rows"
            :per-page="perPage"
            aria-controls="my-table"
          ></b-pagination>
        </div>
      </b-card>
    </div>
    <!-- <b-table striped hover :items="items"></b-table> -->
  </div>
</template>

<script>

import moment from 'moment';
/* eslint-disable no-underscore-dangle */
export default {
  data() {
    return {
      allError: [],
      perPage: 20,
      currentPage: 1,
      perPage2: 10,
      currentPage2: 1,
      // totalData_table: [],
      fields: [
        // {
        //   key: 'classroom',
        //   label: '教室名稱',
        //   sortable: true,
        // },
        {
          key: 'host',
          label: '電腦名稱',
          sortable: false,
        },
        {
          key: 'count',
          label: '錯誤次數',
          sortable: true,
        },
      ],
      fields_total: [
        {
          key: 'date',
          label: '日期',
          sortable: true,
        },
        // {
        //   key: 'classroom',
        //   label: '教室名稱',
        //   sortable: false,
        // },
        {
          key: 'host',
          label: '電腦名稱',
          sortable: true,
        },
        {
          key: 'use_total_time',
          label: '使用時間',
          sortable: true,
        },
        {
          key: 'count',
          label: '使用次數',
          sortable: true,
        },
        {
          key: 'per_average_time',
          label: '平均使用時間',
          sortable: true,
        },
      ],
      date1: '',
      date2: '',
    };
  },
  created() {
  },
  mounted() {
    this.dataSwitch = this.items2;
    this.date1 = this.minDate;
    this.date2 = this.maxDate;
  },
  computed: {
    getQ3() {
      const temp = this.totalData_table.map((e) => e.per_average_time);
      temp.sort((a, b) => a - b);
      const index = (75 / 100) * temp.length;
      let result;
      if (Math.floor(index) === index) {
        result = (parseFloat(temp[(index - 1)]) + parseFloat(temp[index])) / 2;
      } else {
        result = temp[Math.floor(index)];
      }
      return parseFloat(result);
    },
    getQ1() {
      const temp = this.totalData_table.map((e) => e.per_average_time);
      temp.sort((a, b) => a - b);
      const index = (25 / 100) * temp.length;
      let result;
      if (Math.floor(index) === index) {
        result = (parseFloat(temp[(index - 1)]) + parseFloat(temp[index])) / 2;
      } else {
        result = temp[Math.floor(index)];
      }
      return parseFloat(result);
    },
    midataerrortable: {
      get() {
        return this.$store.state.midataerrortable.map((e) => {
          if (e.count >= 10) { // 判斷錯誤次數大於10次的顏色改紅色
            e._rowVariant = 'danger';
          }
          return e;
        });
      },
    },
    totalData_table: {
      get() {
        return this.$store.state.totalData_table;
      },
    },
    rows() { // 總表分頁
      return this.totalData_table2.length;
    },
    rows2() { // 錯誤表分頁
      return this.midataerrortable.length;
    },
    totalData_table2() {
      // eslint-disable-next-line consistent-return
      const temp = this.totalData_table.filter((e) => {
        if (moment(this.date1).isBefore(this.date2)) { // 判斷時間選擇是否選相反
          return moment(e.date).isBetween(this.date1, this.date2, null, []);
        }
        return moment(e.date).isBetween(this.date2, this.date1, null, []);
      });
      temp.map((e) => {
        if (e.per_average_time > this.getQ3) { // 判斷平均使用時數大於Q3的顏色改紅色
          e._rowVariant = 'danger';
        } else if (e.per_average_time < this.getQ1) { // 判斷平均使用時數小於Q1的顏色改綠色
          e._rowVariant = 'success';
        } else {
          e._rowVariant = 'warning'; // 其他改黃色
        }
        return e;
      });
      return temp;
    },
    minDate() {
      return moment.min(this.totalData_table.map((e) => moment(e.date))).format('YYYY-MM-DD');
    },
    maxDate() {
      return moment.max(this.totalData_table.map((e) => moment(e.date))).format('YYYY-MM-DD');
    },
  },
};
</script>

