<template>
  <div>
    <apexchart
      type="heatmap"
      height="350"
      :options="chartOptions2"
      :series="series2"
    ></apexchart>
  </div>
</template>

<script>

export default {
  // extends: TreeMap,
  data() {
    return {
      series2: [
        {
          id: 1,
          name: '一月',
          data: [],
        },
        {
          id: 2,
          name: '二月',
          data: [],
        },
        {
          id: 3,
          name: '三月',
          data: [],
        },
        {
          id: 4,
          name: '四月',
          data: [],
        },
        {
          id: 5,
          name: '五月',
          data: [],
        },
        {
          id: 6,
          name: '六月',
          data: [],
        },
        {
          id: 7,
          name: '七月',
          data: [],
        },
        {
          id: 8,
          name: '八月',
          data: [],
        },
        {
          id: 9,
          name: '九月',
          data: [],
        },
        {
          id: 10,
          name: '十月',
          data: [],
        },
        {
          id: 11,
          name: '十一月',
          data: [],
        },
        {
          id: 12,
          name: '十二月',
          data: [],
        },
      ],
    };
  },
  created() {
    this.series2.forEach((e) => {
      for (let i = 1; i <= 31; i += 1) {
        e.data.push({ x: i, y: 0 });
      }
    });
    this.series2.forEach((item, index) => {
      this.midatatreemap.forEach((e) => {
        if (e.month === index + 1) {
          // console.log(index);
          for (let i = 1; i <= 31; i += 1) {
            if (e.day === i) {
              this.series2[index].data[i - 1] = { x: e.day, y: e.use_total_time };
            }
            // this.series2[index].data.push({ x: e.day, y: e.use_total_time });
          }
        }
      });
    });
  },
  mounted() {
    // console.log(`MIN:${this.getMin}---Q1:${this.getQ1}---MID:${this.getMid}---Q3:${this.getQ3}---MAX:${this.getMax}`);
    // console.log(this.midatatreemap.map((e) => e.use_total_time));
  },
  computed: {
    midatatreemap: {
      get() {
        return this.$store.state.midatatreemap.filter((e) => e.use_total_time > 0); // 去除使用時數為0的資料
      },
    },
    getQ1() { // 取四分位距Q1
      const temp = this.midatatreemap.map((e) => e.use_total_time);
      temp.sort((a, b) => a - b); // 排序，大到小
      const index = (25 / 100) * temp.length; // 取25%的index
      let result;

      // console.log(temp);
      // console.log(`25%:${index}`);
      // console.log(Math.floor(index));

      if (Math.floor(index) === index) {
        result = (temp[(index - 1)] + temp[index]) / 2;
      } else {
        result = temp[Math.floor(index)];
      }
      return parseFloat(result); // 轉成Float格式
    },
    getMid() {
      const temp = this.midatatreemap.map((e) => e.use_total_time);
      temp.sort((a, b) => a - b);
      const index = (50 / 100) * temp.length;
      // console.log(`50%:${index}`);
      let result;
      if (Math.floor(index) === index) {
        result = (temp[(index - 1)] + temp[index]) / 2;
      } else {
        result = temp[Math.floor(index)];
      }
      return parseFloat(result);
    },
    getQ3() {
      const temp = this.midatatreemap.map((e) => e.use_total_time);
      temp.sort((a, b) => a - b);
      const index = (75 / 100) * temp.length;
      let result;
      if (Math.floor(index) === index) {
        result = (temp[(index - 1)] + temp[index]) / 2;
      } else {
        result = temp[Math.floor(index)];
      }
      return parseFloat(result);
    },
    getMax() {
      return Math.max(...this.midatatreemap.map((e) => e.use_total_time));
    },
    getMin() {
      return Math.min(...this.midatatreemap.map((e) => e.use_total_time));
    },
    chartOptions2() {
      const temp = {
        chart: {
          height: 350,
          type: 'heatmap',
          zoom: {
            enabled: false,
          },
        },
        dataLabels: {
          enabled: false,
        },
        title: {
          text: '使用總時數 HeatMap',
        },
        tooltip: {
          custom({
            series, seriesIndex, dataPointIndex, w,
          }) {
            // console.log(w);
            return `<div class="arrow_box"><span>${w.globals.seriesNames[seriesIndex]}${w.globals.seriesX[0][dataPointIndex]}日:總共${series[seriesIndex][dataPointIndex]}小時</span></div>`;
          },
        },
        plotOptions: {
          heatmap: {
            reverseNegativeShade: true,
            radius: 0,
            enableShades: true,
            distributed: true,
            colorScale: {
              ranges: [
                {
                  from: this.getQ3,
                  to: this.getMax,
                  color: '#FF0000',
                  name: '較多',
                },
                {
                  from: this.getMid,
                  to: this.getQ3,
                  color: '#f58142',
                  name: '多',
                },
                {
                  from: this.getQ1,
                  to: this.getMid,
                  color: '#f5ef42',
                  name: '少',
                },
                {
                  from: this.getMin,
                  to: this.getQ1,
                  color: '#00FF00',
                  name: '較少',
                },
              ],
            },
          },
        },
        xaxis: {
          show: true,
          position: 'bottom',
          title: {
            text: '日',
            style: {
              fontSize: '15px',
              fontFamily: '微軟正黑體',
              fontWeight: 600,
              cssClass: 'apexcharts-xaxis-title',
            },
          },
        },
        yaxis: {
          show: true,
          title: {
            text: '月',
            style: {
              fontSize: '15px',
              fontFamily: 'Helvetica, Arial, sans-serif',
              fontWeight: 600,
              cssClass: 'apexcharts-xaxis-title',
            },
          },
        },
      };
      return temp;
    },
  },
};
</script>
