<script>
// import { Line } from 'vue-chartjs';
import { Bar } from 'vue-chartjs';
// eslint-disable-next-line no-unused-vars
import _ from 'lodash';
// import test from '../assets/test.json';
// import barData from '../assets/midatabarchart.json';

export default {
  // extends: Line,
  extends: Bar,
  data() {
    return {
      // data: barData,
      date: ['星期一', '星期二', '星期三', '星期四', '星期五', '星期六', '星期日'],
      average: [],
    };
  },
  computed: {
    // 長條圖資料
    midatabarchart: {
      get() {
        return this.$store.state.midatabarchart;
      },
    },
  },
  mounted() {
    // this.average = this.midatabarchart.map((e) => e.use_average_time);
    const barData = this.midatabarchart.map((e) => ({
      x: e.weekday,
      y: e.use_average_time,
    }));
    // 畫圖
    this.renderChart(
      {
        labels: this.date,
        datasets: [
          {
            label: this.midatabarchart[0].classroom,
            data: barData,
            backgroundColor: 'red',
          },
        ],
      },
      {
        responsive: true,
        maintainAspectRatio: false,
        title: {
          display: true,
          text: '平均使用時間長條圖',
          fontFamily: '微軟正黑體',
          fontSize: 30,
        },
        legend: { // 不要顯示資料集的按鈕
          display: false,
        },
        tooltips: {
          bodyFontSize: 16,
          titleFontSize: 18,
          callbacks: {
            label(tooltipItem, data) {
              let label = data.datasets[tooltipItem.datasetIndex].label || '';
              if (label) {
                label += ': ';
              }
              const value = Math.round(tooltipItem.yLabel * 100) / 100;
              // console.log(label);
              return `平均${value}小時`;
            },
          },
        },
        scales: {
          xAxes: [{
            // stacked: true, // 資料值對X軸
            scaleLabel: {
              display: true,
              labelString: '日期',
            },
          },
          ],
          yAxes: [
            {
              // stacked: true,
              scaleLabel: {
                display: true,
                labelString: '平均時數（小時）',
              },
            },
          ],
        },
      },
    );
  },
};
</script>
